<template>
  <div class="systemConfiguration">
    <el-container>
      <el-header>
        
      </el-header>
      <el-main>
        <el-form ref="form" :model="form">
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="" prop="port" :rules=rule.port>
                <el-select prop="port" v-model="form.port" placeholder="">
                  <el-option label="COM1" value="COM1"></el-option>
                  <el-option label="COM2" value="COM2"></el-option>
                  <el-option label="COM3" value="COM3"></el-option>
                  <el-option label="COM4" value="COM4"></el-option>
                  <el-option label="COM5" value="COM5"></el-option>
                  <el-option label="COM6" value="COM6"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="baudRate" :rules=rule.baudRate>
                <el-select v-model="form.baudRate" placeholder="">
                  <el-option label="300" value="300"></el-option>
                  <el-option label="600" value="600"></el-option>
                  <el-option label="1200" value="1200"></el-option>
                  <el-option label="2400" value="2400"></el-option>
                  <el-option label="4800" value="4800"></el-option>
                  <el-option label="9600" value="9600"></el-option>
                  <el-option label="19200" value="19200"></el-option>
                  <el-option label="38400" value="38400"></el-option>
                  <el-option label="43000" value="43000"></el-option>
                  <el-option label="56000" value="56000"></el-option>
                  <el-option label="57600" value="57600"></el-option>
                  <el-option label="115200" value="115200"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="" prop="dataBit" :rules=rule.dataBit>
                <el-input v-model="form.dataBit" placeholder="">
                </el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="checkBit" :rules=rule.checkBit>
                <el-select v-model="form.checkBit" placeholder="">
                  <el-option label="None" value=""></el-option>
                  <el-option label="1" value="1"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="" prop="stopBit" :rules=rule.stopBit>
                <el-select v-model="form.stopBit" placeholder="">
                  <el-option label="1" value="1"></el-option>
                  <el-option label="2" value="2"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="enable" :rules=rule.enable>
                <el-select v-model="form.enable" placeholder="">
                  <el-option label="" value="1"></el-option>
                  <el-option label="" value="0"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-form-item>
            <el-button type="primary" @click="onSubmit('form')"></el-button>
            <el-button></el-button>
          </el-form-item>
        </el-form>
      </el-main>
    </el-container>
  </div>
</template>

<script type="text/babel">
  import util from '../../utils/util.js';
  import httpserver from '../../utils/http.js';
  import api from '../../utils/api.js';
  // import config from '../../js/config'
  export default {
    data() {
      return {
        form: {
          port: '',
          baudRate: '',
          dataBit: '',
          checkBit: '',
          stopBit: '',
          enable: '',
          terminalCode: JSON.parse(window.localStorage.getItem('terminal')).terminalCode
        },
        rule: {
          port: [
            {required: true, message: '', trigger: 'change'}
          ],
          baudRate: [
            {required: true, message: '', trigger: 'change'}
          ],
          dataBit: [
            {required: true, message: '', trigger: 'blur'}
          ],
//          checkBit:[
//            {required: true, message: '', trigger: 'change'}
//          ],
          stopBit: [
            {required: true, message: '', trigger: 'change'}
          ],
          enable: [
            {required: true, message: '', trigger: 'change'}
          ]
        }

      }
    },
    methods: {
      onSubmit(formName) {
        this.$refs[formName].validate(async (valid) => {
          if (valid) {
            httpserver(api.saveSystemCom, this.form)
              .then((response) => {
                if (response.data.returnCode == "0") {
                  this.$message({
                    message: '',
                    type: 'success'
                  })
                  window.localStorage.setItem('serialPort', JSON.stringify(this.form));
                  let loc = JSON.parse(window.localStorage.getItem('terminal'));
                  if (loc.homePage == null || loc.homePage == undefined || loc.homePage == "") {
                    this.$message({
                      message: '',
                      type: 'warning'
                    })
                    this.$router.push('/home');
                  } else {

                    this.$router.push(loc.homePage);
                  }
                }
              })
              .catch((error) => {
                console.log(error.response);
                this.$message({
                  message: error.response.data.errorMessage,
                  type: 'error'
                })
              })
          }
        })
      }
    }
  }
</script>
<style lang="less">
  @import "../../css/system/systemConfiguration.less";
</style>



